import { store, getContext, getElement } from '@wordpress/interactivity';

store('pulsar/facetwp-filter', {
	actions: {
		onFacetwpLoaded: () => {
			const context = getContext();

			let count = 0;
			const excludeFacets = ['pager', 'sort', 'reset'];

			Object.entries(window.FWP.facets).forEach(([name, val]) => {
				const type = window.FWP.facet_type?.[name];
				if (!excludeFacets.includes(type) && val.length > 0) {
					count += val.length;
				}
			});

			context.appliedFilterCount = count;
		},
		setFacetAvailable: () => {
			const context = getContext();
			const { filterId } = context;
			const { ref } = getElement();
			const panel = ref.querySelector(
				'.wp-block-pulsar-facetwp-filter__panel'
			);
			if (!panel) return false;
			const facet = panel.querySelector(
				`.facetwp-facet[data-name="${filterId}"]`
			);
			if (!facet) return false;

			context.isFacetAvailable = facet.children.length > 0;
		},
		setInModal: () => {
			const context = getContext();
			const { ref } = getElement();
			const items = ref.closest(
				'.wp-block-pulsar-facetwp-filters__items'
			);

			context.inModal = !!items;
		},
	},
	callbacks: {
		init: () => {
			const { actions } = store('pulsar/facetwp-filter');
			actions.setInModal();
			actions.onFacetwpLoaded();
			actions.setFacetAvailable();
		},
		isFacetAvailable: () => {
			const context = getContext();
			const { isFacetAvailable } = context;
			return isFacetAvailable;
		},
		isInModal: () => {
			const context = getContext();
			const { inModal } = context;
			return inModal;
		},
	},
});
